'use client';

import { useState, useEffect, useCallback } from 'react';
import { cronJobsApi, CronJobSummary, CronJobRun, CronJobsResponse, ApiError } from '@/lib/api-client';
import {
  Clock,
  Loader2,
  AlertCircle,
  AlertTriangle,
  CheckCircle,
  XCircle,
  RefreshCw,
  Play,
  Timer,
  Activity,
  Zap,
  Calendar,
  ChevronDown,
  ChevronUp,
} from 'lucide-react';

type JobStatus = 'pending' | 'running' | 'completed' | 'failed' | 'timeout';

export default function CronJobsPage() {
  const [data, setData] = useState<CronJobsResponse | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [expandedJob, setExpandedJob] = useState<string | null>(null);
  const [triggeringJob, setTriggeringJob] = useState<string | null>(null);
  const [lastRefresh, setLastRefresh] = useState<Date | null>(null);
  const [autoRefresh, setAutoRefresh] = useState(true);

  const loadData = useCallback(async () => {
    try {
      setError(null);
      const response = await cronJobsApi.list({ includeRuns: true, limit: 100 });
      setData(response);
      setLastRefresh(new Date());
    } catch (err) {
      if (err instanceof ApiError) {
        const errorMsg = err.status === 401
          ? 'Please log in to view scheduled jobs.'
          : err.status === 403
          ? 'Access denied. Admin access may be required.'
          : `${err.message} (Status: ${err.status})`;
        setError(errorMsg);
      } else if (err instanceof Error) {
        setError(`Failed to load data: ${err.message}`);
      } else {
        setError('Failed to load data: Unknown error');
      }
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    loadData();
  }, [loadData]);

  // Auto-refresh every 30 seconds
  useEffect(() => {
    if (!autoRefresh) return;
    const interval = setInterval(loadData, 30000);
    return () => clearInterval(interval);
  }, [autoRefresh, loadData]);

  const handleTriggerJob = async (jobId: string) => {
    setTriggeringJob(jobId);
    try {
      await cronJobsApi.trigger(jobId);
      // Wait a moment then refresh to see the new run
      setTimeout(loadData, 2000);
    } catch (err) {
      if (err instanceof ApiError) {
        alert(`Failed to trigger job: ${err.message}`);
      }
    } finally {
      setTriggeringJob(null);
    }
  };

  const getStatusColor = (status: JobStatus) => {
    switch (status) {
      case 'completed':
        return 'text-green-400 bg-green-900/20 border-green-700';
      case 'running':
        return 'text-blue-400 bg-blue-900/20 border-blue-700';
      case 'pending':
        return 'text-yellow-400 bg-yellow-900/20 border-yellow-700';
      case 'failed':
      case 'timeout':
        return 'text-red-400 bg-red-900/20 border-red-700';
      default:
        return 'text-gray-400 bg-gray-900/20 border-gray-700';
    }
  };

  const getStatusIcon = (status: JobStatus) => {
    switch (status) {
      case 'completed':
        return <CheckCircle className="w-4 h-4" />;
      case 'running':
        return <Loader2 className="w-4 h-4 animate-spin" />;
      case 'pending':
        return <Clock className="w-4 h-4" />;
      case 'failed':
      case 'timeout':
        return <XCircle className="w-4 h-4" />;
      default:
        return <Activity className="w-4 h-4" />;
    }
  };

  const formatDuration = (ms: number | null) => {
    if (ms === null || ms === undefined) return '-';
    if (ms < 1000) return `${ms}ms`;
    if (ms < 60000) return `${(ms / 1000).toFixed(1)}s`;
    return `${Math.round(ms / 60000)}m ${Math.round((ms % 60000) / 1000)}s`;
  };

  const formatTime = (isoString: string | null) => {
    if (!isoString) return '-';
    const date = new Date(isoString);
    return date.toLocaleString();
  };

  const formatTimeAgo = (isoString: string | null) => {
    if (!isoString) return 'Never';
    const date = new Date(isoString);
    const now = new Date();
    const diffMs = now.getTime() - date.getTime();
    const diffMin = Math.floor(diffMs / 60000);
    const diffHour = Math.floor(diffMin / 60);
    const diffDay = Math.floor(diffHour / 24);

    if (diffMin < 1) return 'Just now';
    if (diffMin < 60) return `${diffMin}m ago`;
    if (diffHour < 24) return `${diffHour}h ago`;
    return `${diffDay}d ago`;
  };

  const getJobHealth = (job: CronJobSummary) => {
    if (job.consecutiveFailures >= 3) return 'critical';
    if (job.consecutiveFailures > 0) return 'warning';
    if (!job.enabled) return 'disabled';
    if (job.stats.successRate === null) return 'unknown';
    if (job.stats.successRate >= 0.95) return 'healthy';
    if (job.stats.successRate >= 0.8) return 'warning';
    return 'critical';
  };

  const getHealthColor = (health: string) => {
    switch (health) {
      case 'healthy':
        return 'text-green-400';
      case 'warning':
        return 'text-yellow-400';
      case 'critical':
        return 'text-red-400';
      case 'disabled':
        return 'text-gray-500';
      default:
        return 'text-gray-400';
    }
  };

  if (loading && !data) {
    return (
      <div className="text-center py-12">
        <Loader2 className="w-8 h-8 animate-spin mx-auto text-lime-400" />
        <p className="mt-4 text-gray-400">Loading scheduled jobs...</p>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold flex items-center gap-3">
            <Calendar className="w-8 h-8 text-lime-400" />
            Scheduled Jobs
          </h1>
          <p className="text-gray-400 mt-1">
            Monitor and manage cron jobs across the system
          </p>
        </div>
        <div className="flex items-center gap-4">
          <label className="flex items-center gap-2 text-sm text-gray-400">
            <input
              type="checkbox"
              checked={autoRefresh}
              onChange={(e) => setAutoRefresh(e.target.checked)}
              className="rounded border-gray-600 bg-gray-800 text-lime-400 focus:ring-lime-400"
            />
            Auto-refresh
          </label>
          <button
            onClick={loadData}
            disabled={loading}
            className="flex items-center gap-2 px-4 py-2 bg-gray-800 hover:bg-gray-700 rounded-lg transition-colors disabled:opacity-50"
          >
            <RefreshCw className={`w-4 h-4 ${loading ? 'animate-spin' : ''}`} />
            Refresh
          </button>
        </div>
      </div>

      {/* Last refresh */}
      {lastRefresh && (
        <p className="text-sm text-gray-500">
          Last updated: {lastRefresh.toLocaleTimeString()}
        </p>
      )}

      {/* Error */}
      {error && (
        <div className="p-4 bg-red-900/20 border border-red-800 rounded-lg flex items-center gap-3">
          <AlertCircle className="w-5 h-5 text-red-400 flex-shrink-0" />
          <span className="text-red-400">{error}</span>
        </div>
      )}

      {data && (
        <>
          {/* Summary Cards */}
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            <div className="bg-gray-900 rounded-lg border border-gray-800 p-4">
              <div className="flex items-center gap-2 text-gray-400 text-sm mb-1">
                <Activity className="w-4 h-4" />
                Total Jobs
              </div>
              <p className="text-2xl font-bold">{data.summary.totalJobs}</p>
            </div>
            <div className="bg-gray-900 rounded-lg border border-gray-800 p-4">
              <div className="flex items-center gap-2 text-gray-400 text-sm mb-1">
                <Zap className="w-4 h-4 text-green-400" />
                Active
              </div>
              <p className="text-2xl font-bold text-green-400">{data.summary.activeJobs}</p>
            </div>
            <div className="bg-gray-900 rounded-lg border border-gray-800 p-4">
              <div className="flex items-center gap-2 text-gray-400 text-sm mb-1">
                <Loader2 className="w-4 h-4 text-blue-400" />
                Running Now
              </div>
              <p className="text-2xl font-bold text-blue-400">{data.summary.runningJobs}</p>
            </div>
            <div className="bg-gray-900 rounded-lg border border-gray-800 p-4">
              <div className="flex items-center gap-2 text-gray-400 text-sm mb-1">
                <AlertTriangle className="w-4 h-4 text-red-400" />
                Failing
              </div>
              <p className="text-2xl font-bold text-red-400">{data.summary.recentlyFailed}</p>
            </div>
          </div>

          {/* Failing Jobs Alert */}
          {data.summary.recentlyFailed > 0 && (
            <div className="bg-red-900/20 border border-red-800 rounded-lg p-4">
              <h3 className="text-lg font-semibold text-red-400 flex items-center gap-2 mb-3">
                <AlertTriangle className="w-5 h-5" />
                Jobs Requiring Attention
              </h3>
              <div className="space-y-2">
                {data.jobs
                  .filter((j) => j.consecutiveFailures > 0)
                  .map((job) => (
                    <div
                      key={job.jobId}
                      className="flex items-center justify-between bg-gray-900/50 rounded-lg p-3"
                    >
                      <div>
                        <span className="font-medium">{job.displayName}</span>
                        <span className="text-gray-400 ml-2">({job.jobId})</span>
                      </div>
                      <div className="flex items-center gap-4">
                        <span className="text-red-400">
                          {job.consecutiveFailures} consecutive failures
                        </span>
                        {job.lastFailureAt && (
                          <span className="text-gray-500 text-sm">
                            Last failed: {formatTimeAgo(job.lastFailureAt)}
                          </span>
                        )}
                      </div>
                    </div>
                  ))}
              </div>
            </div>
          )}

          {/* Jobs Table */}
          <div className="bg-gray-900 rounded-lg border border-gray-800 overflow-hidden">
            <div className="px-6 py-4 border-b border-gray-800">
              <h2 className="text-lg font-semibold">All Scheduled Jobs</h2>
            </div>
            <div className="divide-y divide-gray-800">
              {data.jobs.length === 0 ? (
                <div className="px-6 py-12 text-center text-gray-400">
                  <Clock className="w-12 h-12 mx-auto mb-4 opacity-50" />
                  <p>No scheduled jobs configured</p>
                </div>
              ) : (
                data.jobs.map((job) => {
                  const health = getJobHealth(job);
                  const isExpanded = expandedJob === job.jobId;
                  const jobRuns = data.recentRuns?.filter((r) => r.jobId === job.jobId) || [];

                  return (
                    <div key={job.jobId} className="bg-gray-900">
                      {/* Job Row */}
                      <div
                        className="px-6 py-4 hover:bg-gray-800/50 cursor-pointer transition-colors"
                        onClick={() => setExpandedJob(isExpanded ? null : job.jobId)}
                      >
                        <div className="flex items-center justify-between">
                          <div className="flex items-center gap-4">
                            <div className={`${getHealthColor(health)}`}>
                              {health === 'critical' ? (
                                <XCircle className="w-5 h-5" />
                              ) : health === 'warning' ? (
                                <AlertTriangle className="w-5 h-5" />
                              ) : health === 'disabled' ? (
                                <Clock className="w-5 h-5 opacity-50" />
                              ) : (
                                <CheckCircle className="w-5 h-5" />
                              )}
                            </div>
                            <div>
                              <h3 className="font-medium flex items-center gap-2">
                                {job.displayName}
                                {!job.enabled && (
                                  <span className="text-xs bg-gray-700 text-gray-400 px-2 py-0.5 rounded">
                                    Disabled
                                  </span>
                                )}
                              </h3>
                              <p className="text-sm text-gray-400">
                                {job.description || job.jobId}
                              </p>
                            </div>
                          </div>

                          <div className="flex items-center gap-6">
                            {/* Stats */}
                            <div className="hidden md:flex items-center gap-6 text-sm">
                              <div className="text-center">
                                <p className="text-gray-500">Success Rate</p>
                                <p className={`font-medium ${
                                  job.stats.successRate === null
                                    ? 'text-gray-400'
                                    : job.stats.successRate >= 0.95
                                    ? 'text-green-400'
                                    : job.stats.successRate >= 0.8
                                    ? 'text-yellow-400'
                                    : 'text-red-400'
                                }`}>
                                  {job.stats.successRate !== null
                                    ? `${(job.stats.successRate * 100).toFixed(0)}%`
                                    : '-'}
                                </p>
                              </div>
                              <div className="text-center">
                                <p className="text-gray-500">Avg Duration</p>
                                <p className="font-medium">{formatDuration(job.stats.avgDurationMs)}</p>
                              </div>
                              <div className="text-center">
                                <p className="text-gray-500">Last Run</p>
                                <p className="font-medium">
                                  {job.lastRun ? formatTimeAgo(job.lastRun.startedAt) : 'Never'}
                                </p>
                              </div>
                            </div>

                            {/* Last Run Status */}
                            {job.lastRun && (
                              <div
                                className={`px-3 py-1 rounded-full text-xs flex items-center gap-1 border ${getStatusColor(
                                  job.lastRun.status as JobStatus
                                )}`}
                              >
                                {getStatusIcon(job.lastRun.status as JobStatus)}
                                {job.lastRun.status}
                              </div>
                            )}

                            {/* Trigger Button */}
                            <button
                              onClick={(e) => {
                                e.stopPropagation();
                                handleTriggerJob(job.jobId);
                              }}
                              disabled={triggeringJob === job.jobId}
                              className="flex items-center gap-1 px-3 py-1.5 bg-lime-600 hover:bg-lime-500 text-black text-sm font-medium rounded-lg transition-colors disabled:opacity-50"
                            >
                              {triggeringJob === job.jobId ? (
                                <Loader2 className="w-4 h-4 animate-spin" />
                              ) : (
                                <Play className="w-4 h-4" />
                              )}
                              Run
                            </button>

                            {/* Expand/Collapse */}
                            {isExpanded ? (
                              <ChevronUp className="w-5 h-5 text-gray-400" />
                            ) : (
                              <ChevronDown className="w-5 h-5 text-gray-400" />
                            )}
                          </div>
                        </div>
                      </div>

                      {/* Expanded Details */}
                      {isExpanded && (
                        <div className="px-6 py-4 bg-gray-800/30 border-t border-gray-800">
                          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                            {/* Job Info */}
                            <div>
                              <h4 className="text-sm font-medium text-gray-400 mb-3">
                                Configuration
                              </h4>
                              <div className="space-y-2 text-sm">
                                <div className="flex justify-between">
                                  <span className="text-gray-500">Schedule</span>
                                  <code className="text-lime-400 bg-gray-800 px-2 py-0.5 rounded">
                                    {job.schedule || 'Not set'}
                                  </code>
                                </div>
                                <div className="flex justify-between">
                                  <span className="text-gray-500">Timeout</span>
                                  <span>{formatDuration(job.timeoutMs)}</span>
                                </div>
                                <div className="flex justify-between">
                                  <span className="text-gray-500">Max Retries</span>
                                  <span>{job.maxRetries}</span>
                                </div>
                                <div className="flex justify-between">
                                  <span className="text-gray-500">Alert on Failure</span>
                                  <span>{job.alertOnFailure ? 'Yes' : 'No'}</span>
                                </div>
                                <div className="flex justify-between">
                                  <span className="text-gray-500">Last Success</span>
                                  <span>{formatTimeAgo(job.lastSuccessAt)}</span>
                                </div>
                                <div className="flex justify-between">
                                  <span className="text-gray-500">Last Failure</span>
                                  <span className={job.lastFailureAt ? 'text-red-400' : ''}>
                                    {formatTimeAgo(job.lastFailureAt)}
                                  </span>
                                </div>
                              </div>
                            </div>

                            {/* Recent Runs */}
                            <div>
                              <h4 className="text-sm font-medium text-gray-400 mb-3">
                                Recent Runs ({jobRuns.length})
                              </h4>
                              {jobRuns.length === 0 ? (
                                <p className="text-sm text-gray-500">No runs recorded yet</p>
                              ) : (
                                <div className="space-y-2 max-h-48 overflow-y-auto">
                                  {jobRuns.slice(0, 10).map((run) => (
                                    <div
                                      key={run.id}
                                      className="flex items-center justify-between text-sm bg-gray-800/50 rounded p-2"
                                    >
                                      <div className="flex items-center gap-2">
                                        <div
                                          className={`flex items-center gap-1 ${getStatusColor(
                                            run.status
                                          )} px-2 py-0.5 rounded text-xs`}
                                        >
                                          {getStatusIcon(run.status)}
                                          {run.status}
                                        </div>
                                        <span className="text-gray-500 text-xs">
                                          {run.triggerSource}
                                        </span>
                                      </div>
                                      <div className="flex items-center gap-3">
                                        <span className="text-gray-400">
                                          {formatDuration(run.durationMs)}
                                        </span>
                                        <span className="text-gray-500 text-xs">
                                          {formatTimeAgo(run.startedAt)}
                                        </span>
                                      </div>
                                    </div>
                                  ))}
                                </div>
                              )}
                            </div>
                          </div>

                          {/* Error Message */}
                          {job.lastRun?.errorMessage && (
                            <div className="mt-4 p-3 bg-red-900/20 border border-red-800 rounded text-sm">
                              <p className="text-red-400 font-medium mb-1">Last Error:</p>
                              <code className="text-red-300 text-xs break-all">
                                {job.lastRun.errorMessage}
                              </code>
                            </div>
                          )}
                        </div>
                      )}
                    </div>
                  );
                })
              )}
            </div>
          </div>

          {/* Recent Runs Timeline */}
          {data.recentRuns && data.recentRuns.length > 0 && (
            <div className="bg-gray-900 rounded-lg border border-gray-800 p-6">
              <h2 className="text-lg font-semibold mb-4 flex items-center gap-2">
                <Timer className="w-5 h-5" />
                Recent Activity (Last 50 Runs)
              </h2>
              <div className="overflow-x-auto">
                <table className="w-full text-sm">
                  <thead>
                    <tr className="border-b border-gray-800">
                      <th className="text-left py-2 text-gray-400">Job</th>
                      <th className="text-left py-2 text-gray-400">Status</th>
                      <th className="text-left py-2 text-gray-400">Trigger</th>
                      <th className="text-left py-2 text-gray-400">Duration</th>
                      <th className="text-left py-2 text-gray-400">Started</th>
                      <th className="text-left py-2 text-gray-400">Error</th>
                    </tr>
                  </thead>
                  <tbody>
                    {data.recentRuns.slice(0, 20).map((run) => {
                      const job = data.jobs.find((j) => j.jobId === run.jobId);
                      return (
                        <tr key={run.id} className="border-b border-gray-800">
                          <td className="py-2">
                            <span className="font-medium">{job?.displayName || run.jobId}</span>
                          </td>
                          <td className="py-2">
                            <span
                              className={`flex items-center gap-1 w-fit px-2 py-0.5 rounded text-xs ${getStatusColor(
                                run.status
                              )}`}
                            >
                              {getStatusIcon(run.status)}
                              {run.status}
                            </span>
                          </td>
                          <td className="py-2 text-gray-400">{run.triggerSource}</td>
                          <td className="py-2">{formatDuration(run.durationMs)}</td>
                          <td className="py-2 text-gray-400">{formatTime(run.startedAt)}</td>
                          <td className="py-2">
                            {run.errorMessage ? (
                              <span className="text-red-400 truncate max-w-xs block" title={run.errorMessage}>
                                {run.errorMessage.substring(0, 50)}...
                              </span>
                            ) : (
                              <span className="text-gray-500">-</span>
                            )}
                          </td>
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
              </div>
            </div>
          )}
        </>
      )}
    </div>
  );
}







